.about__container {
  display: grid;
  grid-template-columns: 35% 50%;
  gap: 15%;
}
.about__me {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(
    45deg,
    transparent,
    var(--color-primary),
    transparent
  );
  display: grid;
  place-items: center;
}
.about__me-image {
  border-radius: 2rem;
  overflow: hidden;
  transform: rotate(10deg);
  transition: var(--transition);
}
.about__me-image:hover {
  transform: rotate(0deg);
}
.about__content p {
  margin-bottom: 2.6rem;
  color: var(--color-light);
}

/*media query medium devices*/
@media screen and (max-width: 1024px) {
  .about__container {
    grid-template-columns: 1fr;
    gap: 0;
  }
  .about__me {
    width: 40%;
    margin: 2rem auto 4rem;
  }
  .about__content p {
    margin-bottom: 1.5rem;
  }
}
/*media query small devices*/
@media screen and (max-width: 600px) {
  .about__me {
    width: 55%;
    margin: 0 auto 3rem;
  }
  .about__content {
    text-align: center;
  }
  .about__content p {
    margin: 1.5rem 0;
  }
}
